<template>
    <div>
        <el-form ref="addForm" :rules="rules" :model="dataForm" label-width="150px">
            <el-row>
                <el-col :span="22">
                    <el-form-item label="组件名称" prop="componentsName">
                        <el-input type="text" v-model="dataForm.componentsName" maxlength='10' placeholder="请输入组件名称"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="22">
                    <el-form-item label="组件类型" prop="componentsType">
                            <el-select v-model="dataForm.componentsType" placeholder="请选择" style="width:100%" >
                                <el-option  v-for="item in subTypeList" :key="item.itemCode" :label="item.itemName" :value="item.itemCode"></el-option>
                            </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
             <el-row>
                <el-col :span="22">
                    <el-form-item label="关联元数据" prop="tabId">
                        <el-select v-model="dataForm.tabId" placeholder="请选择" style="width:100%">
                            <el-option  v-for="item in tabList" :key="item.tabId" :label="item.tabName" :value="item.tabId"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="22">
                    <el-form-item label="组件说明" prop="componentsDesc">
                        <el-input type="textarea" :rows="5" v-model="dataForm.componentsDesc" ></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <div class="dialog-footer">
                <el-button type="primary" @click="saveArea()">保存</el-button>
                <el-button type="danger" @click="cancle()">取消</el-button>
            </div>
        </el-form>
    </div>
</template>

<script>
    export default{
        props: [
            'checkedData',
            'isEdit'
        ],
        data(){
            return{
                //表单验证
                rules: {
                    componentsName: [{
                        required: true,
                        message: '请输入组件名称',
                        trigger: 'change'
                    }],
                },
                tabList:'',
                subTypeList:'',
                dataForm:{
                    componentsName: "",
                    componentsType:"",
                    componentsDesc:'',
                    tabId:'',
                    componentsId:''
                },
            }
        },
        created(){ 
            this.setFormData();
            this.getTabList();
            this.getSubType();
        },
        methods:{
            setFormData(){
                if(this.isEdit === false){
                    this.dataForm.componentsName = this.checkedData.componentsName //组件名称
                    this.dataForm.componentsType = this.checkedData.componentsType//组件类型
                    this.dataForm.componentsDesc = this.checkedData.componentsDesc//组件说明
                    this.dataForm.tabId = this.checkedData.tabId//元数据id
                    this.dataForm.componentsId = this.checkedData.componentsId//组件id
                }
            },
            //数据类型
            getSubType(){
                this.$http({
                    url:this.$http.adornUrl(this.API.getDictItemTable),
                    method:'post',
                    data:this.$http.adornData({
                        dictCode:'SubType'
                    })
                }).then(({data}) => {
                    if(data.code == 0){
                        this.subTypeList = data.data.data;
                    } else {
                        this.$message.error(data.meg)
                    }
                })
            },
            //元数据
            getTabList(){
                this.$http({
                    url:this.$http.adornUrl(this.API.getDataTableInfo),
                    method:'post',
                    data:this.$http.adornData({
                       
                    })
                }).then(({data}) => {
                    if(data.code == 0){
                        this.tabList = data.data;
                    } else {
                        this.$message.error(data.meg)
                    }
                })
            },
            //保存
            saveArea(){
                this.$refs['addForm'].validate((valid) => {
                    if(valid) { 
                        this.$http({
                            url: this.$http.adornUrl(this.API.savaOrUpdateComponents),
                            method: 'post',
                            data: this.$http.adornData(
                                this.dataForm
                            )
                        }).then((res) =>{
                            var resData = res.data;
                            if(resData.code == 0) {
                                this.$message({
                                    message: '成功',
                                    type: 'success'
                                });
                                this.$emit("addback",true);
                            } else {
                                this.$message.error(resData.msg);
                            }
                        });
                    } 
                })
            },
            //取消
            cancle() {
                this.$emit("addback", false);
            } 
        }
    }
</script>

<style>
</style>



