<template>
  <el-container>
    <div style="width: 100%;">
        <div class="table-header-btn">
            <div class="table-header-btn-l">
                <el-input size="small" v-model="componentsName" placeholder="请输入组件名称" clearable></el-input>
                <el-button type="primary" size="small" @click="search()">检索</el-button>
            </div>
            <div class="table-header-btn-r">
                <el-button type="primary" size="small" @click="addClick()">添加</el-button>
                <el-button type="primary" size="small" @click="moveDirection('up')">上移</el-button>
                <el-button type="primary" size="small" @click="moveDirection('down')">下移</el-button>
                <el-button type="primary" size="small" @click="sortClick()">保存排序</el-button>
            </div>
        </div>
        <el-table
            ref="subsystemIndexTable"
            :data="tableData"
            :height="siteContentViewHeight - 100 +'px'"
            @selection-change="selectionChangeHandle"
            v-loading='tableDataLoading'
        >
            <el-table-column type="selection" width="50"></el-table-column>
            <el-table-column label="序号" align="center" width="50">
                <template slot-scope="scope"> {{pageObj.pageIndex*pageObj.pageSize+scope.$index+1}} </template>
            </el-table-column>
            <el-table-column label="组件名称" prop="componentsName"></el-table-column>
            <el-table-column label="组件类型"  prop="componentsType">
                <template slot-scope="scope">
                    <div v-if="parseInt(scope.row.componentsType)===0">列表视图类型</div>
                    <div v-if="parseInt(scope.row.componentsType)===1">照片视图类型</div>
                </template>
            </el-table-column>
            <el-table-column label="操作" width="300">
                <template slot-scope="scope">
                <el-button type="primary" size="mini" plain @click="customClick(scope.$index,scope.row)">定制</el-button>
                <el-button type="primary" size="mini" plain @click="editClick(scope.$index,scope.row)">修改</el-button>
                <el-button type="danger"  size="mini" plain @click="deleClick(scope.$index,scope.row)">删除</el-button>
                </template>
            </el-table-column>

        </el-table>
        <!-- 分页 -->
        <div>
            <pagination :pageIndex="pageObj.pageIndex" :total="pageObj.total" :pageSize="pageObj.pageSize" @PageTurning="PageTurning"></pagination>
	    </div>
    </div>

    <!-- 添加、修改弹框 -->
    <el-dialog
        :title="title"
        width="50%"
        :visible.sync="addModuleDialogVisiable"
        v-if='addModuleDialogVisiable'
        :close-on-click-modal="false"
        :modal-append-to-body='false'
        append-to-body
    >
      <addModuleDialog @addback='addback' :checkedData="checkedData" :isEdit="isEdit"></addModuleDialog>
    </el-dialog>
    <!-- 列表视图定制弹框 -->
    <el-dialog
        :title="title"
        width="100%"
        fullscreen
        :visible.sync="listCustomDialogVisiable"
        v-if='listCustomDialogVisiable'
        :close-on-click-modal="false"
        :modal-append-to-body='false'
        append-to-body
    >
      <listCustomDialog @addback='addback' :checkedData="checkedData"></listCustomDialog>
    </el-dialog>
    <!-- 照片视图定制弹框 -->
    <el-dialog
        :title="title"
        width="100%"
        fullscreen
        :visible.sync="photoCustomDialogVisiable"
        v-if='photoCustomDialogVisiable'
        :close-on-click-modal="false"
        :modal-append-to-body='false'
        append-to-body
    >
      <photoCustomDialog @addback='addback' :checkedData="checkedData"></photoCustomDialog>
    </el-dialog>
    <!-- 信息集视图定制弹框 -->
    <el-dialog
        :title="title"
        width="100%"
        fullscreen
        :visible.sync="informationSetDialogVisiable"
        v-if='informationSetDialogVisiable'
        :close-on-click-modal="false"
        :modal-append-to-body='false'
        append-to-body
    >
      <informationSetDialog @addback='addback' :checkedData="checkedData"></informationSetDialog>
    </el-dialog>

  </el-container>
</template>

<script>
  import {swapItems} from '@/utils';
  import pagination from '@/components/baseComponents/Pagination.vue'
  import addModuleDialog from './add-module-dialog.vue'
  import listCustomDialog from './list-custom-dialog.vue'
  import photoCustomDialog from './photo-custom-dialog.vue'
  import informationSetDialog from './informationSet-custom-dialog.vue'

  export default{
    name:'module-customization-index',
    props:['siteContentViewHeight'],
    components: {
        pagination,
        addModuleDialog,
        listCustomDialog,
        photoCustomDialog,
        informationSetDialog
    },
    data(){
        return{
            pageObj: {
                pageIndex:this.API.constObj.pageIndex, //当前页码
                pageSize:this.API.constObj.pageSize, //页大小
                total:0, //数据总数
            },
            tableData:[],
            addModuleDialogVisiable: false,//添加、修改弹框
            listCustomDialogVisiable:false,
            photoCustomDialogVisiable:false,
            informationSetDialogVisiable:false,
            tableDataLoading:false,
            isEdit: '',
            checkedData:{},//编辑、删除选中数据
            tableDataSelections:[],
            componentsName:''
        }
    },
    created () {
      this.getDataList()
    },
    methods:{
        getDataList(){
            this.tableDataLoading = true;
            this.$http({
                url: this.$http.adornUrl(this.API.getComponentsPageComponents),
                method: 'post',
                data: this.$http.adornData({
                    size:this.pageObj.pageSize,
                    current:this.pageObj.pageIndex + 1,
                    keyword:this.componentsName
                })
            }).then(({data}) =>{
                if(data.code === 0) {
                    this.tableData = data.data.records;
                    this.pageObj.total = data.data.total;
                } else {
                    this.$message.error(data.msg);
                }
                this.tableDataLoading = false;
            })
        },
        //定制
        customClick(index,row){
            if(row.componentsId == '4030C2A0-2FD5-4127-BB1B-F34E60F2BACF'){
                this.checkedData = row;
                this.title = "列表视图定制";
                this.listCustomDialogVisiable = true;
            }
            if(row.componentsId == 'EF3B3950-07C4-40D8-B14D-44420E1FFC4A'){
                this.checkedData = row;
                this.title = "照片视图定制";
                this.photoCustomDialogVisiable = true;
            }
            if(row.componentsId == 'E98D9F0B-DA68-4776-972A-6D4767202BFC'){
                this.checkedData = row;
                this.title = "信息集视图定制";
                this.informationSetDialogVisiable = true;
            }
        },
        //添加
        addClick(){
            this.title = "添加";
            this.isEdit = true;
            this.addModuleDialogVisiable = true;
        },
        //编辑
        editClick(index,row){
            this.checkedData = row;
            this.title = "编辑";
            this.isEdit = false;
            this.addModuleDialogVisiable = true;
        },
        //删除
        deleClick(index,row) {
            this.$confirm('确定删除选中记录?', '操作提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                    this.$http({
                        url: this.$http.adornUrl(this.API.deleteComponents),
                        method: 'post',
                        data: this.$http.adornData({
                            componentsId:row.componentsId
                        })
                }).then(({data}) =>{
                    if(data.code === 0) {
                        this.getDataList();
                        this.$message.success('删除成功');
                    } else {
                        this.$message.error(data.msg);
                    }
                })
            }).catch(() => {

            });
        },

        selectionChangeHandle(val) {//多选
            this.tableDataSelections = val;
        },
        moveDirection(direction){ //移动
            if(this.tableDataSelections.length === 0) {
            this.$confirm('请选择一条数据！', '提示', {
                confirmButtonText: '确定',
                showCancelButton: false,
                type: 'warning',
                center: true
            });
            return;
            };
            var that=this,index;
            that.tableDataSelections.sort((prev,next)=>{
                return that.tableData.indexOf(prev)-that.tableData.indexOf(next)
            })
            if(direction=='down') that.tableDataSelections.reverse()
            that.tableDataSelections.forEach(item=>{
                index=that.tableData.indexOf(item); //得到右侧选中值的标识坐标
                if(direction=='up') that.moveUp(index);
                else if(direction=='down') that.moveDown(index)
            })
        },
        moveUp(index){//上移
            if(index==0) return;
            this.tableData=swapItems(this.tableData,index,index-1,'up');
        },
        moveDown(index){ //下移
            if(index==this.tableData.length-1) return;
            this.tableData=swapItems(this.tableData,index,index+1,'down');
        },
        sortClick(){//保存排序
            var sortArr=[];
            if(this.tableData.length<0) return;
            this.tableData.forEach((item,index)=>{
                sortArr.push({
                    id:item.componentsId,
                    orderno:index+1
                })
            })
            this.$http({
                url: this.$http.adornUrl(this.API.dataComponentsSort),
                method: 'post',
                data: this.$http.adornData({
                    data:JSON.stringify(sortArr)
                })
            }).then(({data}) => {
                if(data && data.code===0){
                    this.getDataList()
                    this.$message.success('操作成功');
                }else{
                    this.$message.error(data.msg);
                }
            });
        },

        search(){
            this.getDataList()
        },

        // 添加或修改组件回调方法--子组件回调数据的方法
	    // @param {Boolean} isRefresh是否需要刷新数据 true=刷新；false=不刷新
        addback(isRefresh){
            this.addModuleDialogVisiable = false;
            if(isRefresh){
                this.getDataList();
            }
        },
        //分页组件回调方法
        PageTurning(page_obj) {
            this.pageObj.pageIndex = page_obj.page_index;
            this.pageObj.pageSize = page_obj.page_size;
            this.getDataList();
        },
    }
  }
</script>

<style scoped lang="scss">
  .table-header-btn-l{
    .el-input{
      margin-right: 10px;
    }
  }
</style>



