<template>
    <el-container class="box">
        <el-main>
            <div class="hed-btn">
                <el-button type="primary" @click="saveClick()">保存</el-button>
            </div>

            <el-table
                id="applyTable"
                border
                class="fixedtableHeight"
                ref="dragTable"
                :data="dataList"
                :header-cell-style="{border:'1px solid #ccc'}"
                tooltip-effect="dark"
                style="width: 100%;margin-top:1%;"
                v-loading="loading"
            >
                <!-- <el-table-column label="序号" type="index" width="50" align="center"></el-table-column> -->
                <el-table-column prop="photoWidth" label="照片宽度(px)">
                    <template slot-scope="scope">
                        <el-input v-model="scope.row.photoWidth" placeholder="请输入"></el-input>
                    </template>
                </el-table-column>
                <el-table-column prop="photoHeight" label="照片高度(px)">
                    <template slot-scope="scope">
                        <el-input v-model="scope.row.photoHeight" placeholder="请输入"></el-input>
                    </template>
                </el-table-column>
              <el-table-column prop="photoShowrows" label="文本行数(行)">
                <template slot-scope="scope">
                  <el-input-number v-model="scope.row.photoShowrows" controls-position="right"  :min="1" :max="100"></el-input-number>
                </template>
              </el-table-column>
              <el-table-column prop="photoWidthrow" label="列宽(%)">
                <template slot-scope="scope">
                  <el-input v-model="scope.row.photoWidthrow" placeholder="请输入"></el-input>
                </template>
              </el-table-column>
              <el-table-column prop="photoHeightrow" label="行高(px)">
                <template slot-scope="scope">
                  <el-input v-model="scope.row.photoHeightrow" placeholder="请输入"></el-input>
                </template>
              </el-table-column>

            </el-table>
        </el-main>
    </el-container>
</template>
<script>
    export default{
        props:[
            'checkedData'
        ],
        data() {
            return {
                optionalColumnList: [],
                tableColumnList: [],
                dataList: [
                    // {
                    //   photoWidth:100,
                    //   photoHeight:200,
                    //   photoColumnnum:4,
                    //   photoLinenum:3,
                    // }
                ],
                loading:true
            }
        },

        created () {
            this.getTableData()
        },
        methods:{
            getTableData(){
              this.$http({
                url: this.$http.adornUrl(this.API.getDataComponentsPhoto),
                method: 'post',
                data: this.$http.adornData({
                  componentsId:this.checkedData.componentsId,
                  photoFlog:'0'
                })
              }).then(({data}) =>{
                if(data.code === 0) {
                  if(data.data == {}){
                    this.dataList = [{
                      photoWidth:'100',
                      photoHeight:'200',
                      photoShowrows:'4',
                      photoWidthrow:'50',
                      photoHeightrow:'110'
                    }]
                  }else{
                    let a = []
                    a.push(data.data)
                    this.dataList = a;
                  }
                  this.loading = false
                } else {
                  this.$message.error(data.msg);
                }
              })
            },
            saveClick(){
                this.$http({
                    url: this.$http.adornUrl(this.API.saveDataComponentsPhoto),
                    method: 'post',
                    data: this.$http.adornData({
                        photoWidth:this.dataList[0].photoWidth,
                        photoHeight:this.dataList[0].photoHeight,
                        photoShowrows:this.dataList[0].photoShowrows,
                        photoWidthrow:this.dataList[0].photoWidthrow,
                        photoHeightrow:this.dataList[0].photoHeightrow,
                        componentsId:this.checkedData.componentsId,

                    })
                }).then(({data}) =>{
                    if(data.code === 0) {
                        this.optionalColumnList = data.data;
                        this.$message.success('保存成功');
                    } else {
                        this.$message.error(data.msg);
                    }
                })
            },

        }
    }
</script>
<style lang="scss" scoped>
    .box{
        .el-main{padding: 0;}
        .hed-btn{
            float: right;
            padding: 0 0 10px 0;
            .el-dropdown{
                padding:0 10px 0 0;

            }
        }
        .el-main{padding-bottom: 0;}
    }
    >>>.el-input-number--medium{
      width: 100%;
    }
</style>



