<template>
    <el-container class="box">
        <el-main>
            <div class="hed-btn">
                <el-dropdown>
                    <el-button type="primary">
                        添加列<i class="el-icon-arrow-down el-icon--right"></i>
                    </el-button>
                    <el-dropdown-menu slot="dropdown" style="width:220px;">
                        <div class="dropdown-menu-box">
                          <el-checkbox-group v-model="checkArr" style="padding:10px 0 0 20px;">
                            <el-col  v-for="item in optionalColumnList" :key="item.colCode">
                              <el-checkbox :label="item.colId" :key="item.colId" @change="menuChange(item)" >{{item.colName}}</el-checkbox>
                            </el-col>
                          </el-checkbox-group>
                        </div>
                    </el-dropdown-menu>
                 </el-dropdown>
                <el-button type="primary" @click="saveClick()">保存</el-button>
            </div>

            <el-table
                id="applyTable"
                @header-dragend="colChange"
                border
                class="fixedtableHeight"
                ref="dragTable"
                :data="dataList"
                :header-cell-style="{border:'1px solid #ccc'}"
                tooltip-effect="dark"
                v-loading="loading"
                style="width: 100%;margin-top:1%;"
            >
                <el-table-column label="序号" type="index" width="50" align="center"></el-table-column>
                <el-table-column prop="listTitle" label="列名">
                    <template slot-scope="scope">
                        <el-input v-model="scope.row.listTitle" placeholder="请输入"></el-input>
                    </template>
                </el-table-column>
                <el-table-column prop="listAlign" label="对齐方式">
                        <template slot-scope="scope">
                            <el-select v-model="scope.row.listAlign" placeholder="请选择" clearable style="width:100%">
                                <el-option
                                    v-for="item in align"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value"
                                >
                                </el-option>
                            </el-select>
                        </template>
                </el-table-column>
                <el-table-column prop="listVertical" label="对齐方式">
                        <template slot-scope="scope">
                            <el-select v-model="scope.row.listVertical" placeholder="请选择" clearable style="width:100%">
                                <el-option
                                    v-for="item in vertical"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value"
                                >
                                </el-option>
                            </el-select>
                        </template>
                </el-table-column>
                <el-table-column prop="listIsorder" label="是否排序">
                        <template slot-scope="scope">
                            <el-select v-model="scope.row.listIsorder" placeholder="请选择" clearable style="width:100%">
                                <el-option
                                    v-for="item in sort"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value"
                                >
                                </el-option>
                            </el-select>
                        </template>
                </el-table-column>
                <el-table-column prop="listWidth" label="列宽(px)">
                    <template slot-scope="scope">
                        <el-input v-model="scope.row.listWidth" placeholder="请输入"></el-input>
                    </template>
                </el-table-column>
                <!-- <el-table-column
                    v-for="(item,index) in tableColumnList"
                    :key="`tableColumnList_${index}`"
                    :prop="tableColumnList[index].colCode"
                    :label="item.colName"
                    :property="item.colCode"
                    align="center"
                    :width="item.width"
                >
                    <template  slot-scope="scope">
                        <el-input v-if="scope.row.flog == 2" v-model="scope.row[scope.column.property]"></el-input>
                        <el-select v-if="scope.row.flog == 3" v-model="scope.row[scope.column.property]" placeholder="请选择" clearable style="width:100%">
                            <el-option
                                v-for="item in options1"
                                :key="item.value"
                                :label="item.label"
                                :value="item.label"
                            >
                            </el-option>
                        </el-select>
                        <el-select v-if="scope.row.flog == 4" v-model="scope.row[scope.column.property]" placeholder="请选择" clearable style="width:100%">
                            <el-option
                                v-for="item in options2"
                                :key="item.value"
                                :label="item.label"
                                :value="item.label"
                            >
                            </el-option>
                        </el-select>
                        <el-select v-if="scope.row.flog == 5" v-model="scope.row[scope.column.property]" placeholder="请选择" clearable style="width:100%">
                            <el-option
                                v-for="item in options3"
                                :key="item.value"
                                :label="item.label"
                                :value="item.label"
                            >
                            </el-option>
                        </el-select>
                        <el-input v-if="scope.row.flog == 1"  v-model="scope.row[scope.column.property]"></el-input>
                    </template>
                </el-table-column> -->
            </el-table>
        </el-main>
    </el-container>
</template>
<script>
    import Sortable from 'sortablejs'
    export default{
        props:[
            'checkedData'
        ],
        data() {
            return {
                optionalColumnList: [],
                tableColumnList: [],
                dataList: [],
                sort:[
                    {
                        value: '0',
                        label: '可排序'
                    },
                    {
                        value: '1',
                        label: '不可排序'
                    },
                ],
                align:[
                    {
                        value: 'left',
                        label: '居左'
                    },
                    {
                        value: 'center',
                        label: '居中'
                    },
                    {
                        value: 'right',
                        label: '居右'
                    },
                ],
                vertical:[
                    {
                        value: 'top',
                        label: '居上'
                    },
                    {
                        value: 'center',
                        label: '居中'
                    },
                    {
                        value: 'bottom',
                        label: '居下'
                    },
                ],
                checkArr:[],//获取选中的
                loading:true
            }
        },

        created () {
            this.getColumnList()
            this.getTableData()
            // this.getTableColWidth()
            // this.columnDrop()
        },
        methods:{
            getColumnList(){
                this.$http({
                    url: this.$http.adornUrl(this.API.getDataTableColumn),
                    method: 'post',
                    data: this.$http.adornData({
                      tabId:this.checkedData.tabId
                    })
                }).then(({data}) =>{
                    if(data.code === 0) {
                        this.optionalColumnList = data.data;
                    } else {
                        this.$message.error(data.msg);
                    }
                })
            },

            getTableData(){
                this.$http({
                    url: this.$http.adornUrl(this.API.getDataComponentsList),
                    method: 'post',
                    data: this.$http.adornData({
                      componentsId:this.checkedData.componentsId,
                      listFlog:'0'
                    })
                }).then(({data}) =>{
                    if(data.code === 0) {
                        // this.tableColumnList = data.data.heads;
                        this.dataList = data.data;
                        this.loading = false
                        this.checkArr=[];
                        this.dataList.forEach((item,index) =>{
                            this.checkArr.push(
                                item.colId
                            )
                        });
                    } else {
                        this.$message.error(data.msg);
                    }
                })
            },
            menuChange(item){
                console.log(item)
                //每一次调用menuChange都要重新获取tableColumnList和dataList，保证属性和数据是对应的
                let flag = true
                for(var i=0;i<this.checkArr.length;i++){
                    if(this.checkArr[i] === item.colId){
                        flag = false
                        break
                    }
                }
                if(!flag){
                    // this.tableColumnList.push(item)
                    var list = {
                        listTitle:item.colName,
                        listAlign:'center',
                        listVertical:'center',
                        listIsorder:'1',
                        listWidth:200,
                        colName: item.colName,
                        colCode:item.colCode,
                        colId:item.colId
                    };
                    this.dataList.push(list);
                }
                if(flag){
                    // Array.prototype.contains = function(obj) {
                    //     var j = this.length;
                    //     while (j--) {
                    //         if (this[j] === obj) {
                    //           console.log(j)
                    //             return j; // 返回的这个就是元素的索引下标，
                    //         }
                    //     }
                    //     return false
                    // }
                    // this.tableColumnList.splice(this.tableColumnList.contains(item),1)
                    // var a = this.dataList.contains(item)
                    //删除表格中未选中数据
                    let itemIndex = 0
                    this.dataList.forEach(function(item1,index){
                      if(item1.colId == item.colId){
                        itemIndex = index
                      }
                    })
                    this.dataList.splice(itemIndex,1)
                }
            },


            saveClick(){
                // var sortArr=[];
                // this.tableColumnList.forEach((item,index)=>{
                //     sortArr.push({
                //         colCode:item.colCode,
                //         colName:item.colName,
                //         colId:item.colId,
                //     })
                // });
                this.$http({
                    url: this.$http.adornUrl(this.API.addDataComponentsList),
                    method: 'post',
                    data: this.$http.adornData({
                        data:JSON.stringify(this.dataList),
                        // headData:JSON.stringify(sortArr),
                        id:this.checkedData.componentsId,
                        code:'0'
                    })
                }).then(({data}) =>{
                    if(data.code === 0) {
                        this.optionalColumnList = data.data;
                        this.$message.success('保存成功');
                    } else {
                        this.$message.error(data.msg);
                    }
                })
            },

            //列拖拽
            columnDrop() {
                const wrapperTr = document.querySelector(' .el-table__header-wrapper tr')
                this.sortable = Sortable.create(wrapperTr, {
                    animation: 180,
                    delay: 0,
                    onEnd: evt => {
                    const oldItem = this.tableColumnList[evt.oldIndex]
                        this.tableColumnList.splice(evt.oldIndex, 1)
                        this.tableColumnList.splice(evt.newIndex, 0, oldItem)
                    }
                })
            },

            /**
             * 获取浏览器缓存的列宽
             */
            getTableColWidth () {
                var self = this
                var tableWidth = localStorage.getItem('applyTableColWidths')

                if (tableWidth) {
                    for (var item = 0; item < self.tableColumnList.length; item++) {
                        self.tableColumnList[item].width = JSON.parse(tableWidth)[item]

                    }
                }
                console.log(JSON.parse(tableWidth)[item])
            },
            /**
             * 拖动表头改变列宽
             */
            colChange (newWidth, oldWidth, column, event) {
                setTimeout(() => {
                    console.log(newWidth, oldWidth, column, event)
                    console.log('拖动表头改变列宽')
                    var applyTableColWidths = []
                    var applyTable = document.getElementById('applyTable')
                    var applyTableColgroup = applyTable.getElementsByTagName('colgroup')[0]
                    var applyTableCol = applyTableColgroup.getElementsByTagName('col')
                    for (var i = 0; i < applyTableCol.length; i++) {
                        applyTableColWidths.push(applyTableCol[i].width)
                    }
                    console.log(JSON.stringify(applyTableColWidths))
                    localStorage.setItem('applyTableColWidths', JSON.stringify(applyTableColWidths))
                }, 100)
            } ,

        }
    }
</script>
<style lang="scss" scoped>
    .box{
        .el-main{padding: 0;}
        .hed-btn{
            float: right;
            padding: 0 0 10px 0;
            .el-dropdown{
                padding:0 10px 0 0;

            }
        }
        .el-main{padding-bottom: 0;}
    }



    .dropdown-menu-box{
      //设置高度才能显示出滚动条 !important
      height:500px;
      overflow: auto;
    }
</style>



